html {
	font-family: 'Roboto', sans-serif; }
nav {
	position: fixed;
	height: 50px;
	width: 100%;
	background: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		font-weight: 300;
		font-size: 15px;
		color: #FFFFFF; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 33px; } }
.menu_item a {
	text-decoration: none; }
.header {
	position: relative; }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0,.6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	.subheader_call {
		margin-top: 19px;
		font-weight: 300;
		font-size: 12px;
		text-align: right;
		color: #FFFFFF; }
	.subheder_phone {
		display: block;
		margin-top: 8px;
		font-weight: 400;
		font-size: 22px;
		line-height: 12px;
		text-align: right;
		text-transform: uppercase;
		color: #FFFFFF;
		text-decoration: none; }
	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		font-weight: 400;
		font-size: 14px;
		text-transform: uppercase;
		color: #FFFFFF; } }
.subheader_official {
	margin-top: 6px;
	opacity: 0.5;
	font-weight: 400;
	font-size: 13px;
	align-items: center;
	color: #FFFFFF; }
.promo {
	background: url('../img/uber_bg.jpg') center center/cover no-repeat;
	min-height: 800px;
	padding: 150px 0 135px 0;
	.promo_header {
		font-weight: 700;
		font-size: 60px;
		line-height: 60px;
		text-transform: uppercase;
		color: #FFFFFF;
		margin: 0;
		margin-top: 67px;
		text-align: center; }
	.promo_subheader {
		font-weight: 700;
		font-size: 36px;
		line-height: 36px;
		text-transform: uppercase;
		color: #FFFFFF;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	.promo_descr {
		margin-top: 23px;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		color: #FFFFFF; }
	.promo_btn {
		display: block;
		margin: 0 auto;
		margin-top: 118px;
		width: 259px;
		height: 63px;
		background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		font-weight: 400;
		font-size: 14px;
		text-transform: uppercase;
		color: #FFFFFF; } }
.reason {
	padding: 71px 0 65px 0;
	.label {
		margin: 0 auto;
		height: 24px;
		width: 113px;
		padding: 5px 12px;
		background-color: black;
		color: white;
		font-weight: 300;
		font-size: 11px;
		text-transform: uppercase; }
	.title {
		margin-bottom: 0;
		margin-top: 38px;
		font-weight: 700;
		font-size: 38px;
		text-align: center;
		text-transform: uppercase;
		color: #222222; }
	.reason_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.reason_descr {
			width: 343px; } }
	.reason_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 116px;
		height: 116px;
		background: #1EACC7;
		border-radius: 100%; }
	.reason_text {
		font-weight: 300;
		line-height: 21px;
		font-size: 15px;
		span {
			color: #1EACC7;
			font-size: 17px;
			line-height: 21px;
			font-weight: 400; } } }
